.product {
  box-sizing: border-box;
  width: $productSize;
  padding: 32px 16px 16px;
  display: flex;
  flex-direction: column;
  flex-shrink: 1;
  flex-grow: 0;
  border: 1px solid #f3f3f3;
  border-radius: 5px;

  &__image {
    height: 134px;
    width: 160px;
    padding-bottom: 40px;
    align-self: center;
  }

  &__name {
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: #060b35;
    text-align: left;
    margin-top: 0;
    margin-bottom: 4px;
  }

  &__code {
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
    color: #616070;
    margin-top: 0;
    margin-bottom: 16px;
  }

  &__rating {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
  }

  &__reviews {
    font-size: 10px;
    font-weight: 400;
    color: #060b35;
    text-decoration: none;
  }


  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
  }
  
  &__price-tag {
      font-weight: normal;
      font-size: 12px;
      line-height: 18px;
      color: #616070;
    }
    
  &__price-number {
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 18px;
    color: #060b35;
  }
  
  &__buy-link {
    box-sizing: border-box;
    width: 166px;
    height: 40px;
    padding: 12px 0;
    text-transform: uppercase;
    background: #00acdc;
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    text-align: center;
    color: #fff;
    border: 1px solid #00acdc;
    border-radius: 5px;
    text-decoration: none;
  
    &:hover {
      color: #00acdc;
      background-color: #fff;
    }
  }
}

